body {
  background-color: #edeef0;
  padding: 20px;
}

@keyframes rotate {
  0% {
    transform: rotate(-.08turn);
  }

  100% {
    transform: rotate(-1.08turn);
  }
}

@keyframes hideEye {
  0%, 10%, 85% {
    height: 50px;
    margin-top: 0;
  }

  30%, 65% {
    height: 30px;
    margin-top: 20px;
  }
}

@keyframes blink {
  0%, 10%, 85% {
    bottom: 0;
  }

  30%, 65% {
    bottom: 20px;
  }

  0% {
    transform: rotate(-.08turn);
  }

  100% {
    transform: rotate(-1.08turn);
  }
}

.loader {
  width: 260px;
  height: 260px;
  margin-top: -130px;
  margin-left: -130px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.loader .track {
  width: 100%;
  height: 100%;
  border-top: 5px solid rgba(0, 0, 0, 0);
  border-bottom: 3px solid #d5afaf;
  border-left: 0 solid #d5afaf;
  border-right: 5px solid #d5afaf;
  border-radius: 50%;
  margin-top: -3px;
  margin-left: -3px;
  animation: rotate 3s linear infinite;
}

.loader .track .mouse {
  width: 25px;
  height: 25px;
  background: #949494;
  border-radius: 80% 0 55% 50% / 55% 0 80% 50%;
  position: absolute;
  top: 18px;
  right: 31px;
  transform: rotate(-95deg);
}

.loader .track .mouse:before, .loader .track .mouse:after {
  content: "";
  width: 9px;
  height: 9px;
  background: inherit;
  border-radius: 50%;
  position: absolute;
}

.loader .track .mouse:before {
  top: -4px;
  left: 5px;
}

.loader .track .mouse:after {
  top: 11px;
  left: 20px;
}

.loader .face {
  width: 130px;
  height: 130px;
  margin-top: -65px;
  margin-left: -65px;
  position: absolute;
  top: 50%;
  left: 50%;
}

.loader .face:before, .loader .face:after {
  content: "";
  width: 50%;
  height: 100%;
  position: absolute;
}

.loader .face:before {
  background: #c8c6c9;
  border-top-left-radius: 65px;
  border-bottom-left-radius: 55px;
}

.loader .face:after {
  background: #d0ced1;
  border-top-right-radius: 65px;
  border-bottom-right-radius: 55px;
  left: 50%;
}

.loader .face .ears-container {
  width: 130px;
  height: 50px;
  position: absolute;
  top: -8px;
}

.loader .face .ears-container:before, .loader .face .ears-container:after {
  content: "";
  width: 0;
  height: 0;
  border-top: 35px solid rgba(0, 0, 0, 0);
  border-bottom: 35px solid rgba(0, 0, 0, 0);
  position: absolute;
}

.loader .face .ears-container:before {
  border-left: 35px solid #c8c6c9;
}

.loader .face .ears-container:after {
  border-right: 35px solid #d0ced1;
  right: 0;
}

.loader .face .eyes-container {
  width: 106px;
  height: 50px;
  z-index: 1;
  margin-left: -53px;
  animation: hideEye 3s linear infinite;
  position: absolute;
  top: 30px;
  left: 50%;
  overflow: hidden;
}

.loader .face .eyes-container .eye {
  float: left;
  width: 50px;
  height: 50px;
  color: #c8c6c9;
  background: #fff;
  border-radius: 50%;
  animation: blink 3s linear infinite;
  position: relative;
  bottom: 0;
}

.loader .face .eyes-container .eye:after {
  content: "";
  width: 12px;
  height: 12px;
  border-radius: inherit;
  background: #838091;
  position: absolute;
  top: 4px;
  right: 14px;
}

.loader .face .eyes-container .eye:last-child {
  float: right;
  color: #d0ced1;
}

.loader .face .phiz {
  width: 32px;
  height: 48px;
  z-index: 1;
  margin-left: -16px;
  position: absolute;
  top: 66px;
  left: 50%;
}

.loader .face .phiz .nose {
  width: 100%;
  height: 15px;
  background: #838091;
  border-radius: 5px 5px 25px 25px;
}

.loader .face .phiz .lip {
  width: 4px;
  height: 12px;
  background: #838091;
  margin-left: -2px;
  position: relative;
  left: 50%;
}

.loader .face .phiz .lip:before {
  content: "";
  width: 100%;
  height: 5px;
  background: #767385;
  position: absolute;
}

.loader .face .phiz .mouth {
  width: 20px;
  height: 6px;
  background: #fff;
  border: 3px solid #838091;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  margin-left: -13px;
  position: relative;
  left: 50%;
}

